import React from 'react';

// legacy visualizations
import Table from '@splunk/dashboard-visualizations/Table';
import Area from '@splunk/dashboard-visualizations/Area';
import Bar from '@splunk/dashboard-visualizations/Bar';
import Bubble from '@splunk/dashboard-visualizations/Bubble';
import Column from '@splunk/dashboard-visualizations/Column';
import FillerGauge from '@splunk/dashboard-visualizations/FillerGauge';
import Line from '@splunk/dashboard-visualizations/Line';
import MarkerGauge from '@splunk/dashboard-visualizations/MarkerGauge';
import Pie from '@splunk/dashboard-visualizations/Pie';
import Punchcard from '@splunk/dashboard-visualizations/Punchcard';
import RadialGauge from '@splunk/dashboard-visualizations/RadialGauge';
import Scatter from '@splunk/dashboard-visualizations/Scatter';
import SingleValue from '@splunk/dashboard-visualizations/SingleValue';
import SingleValueIcon from '@splunk/dashboard-visualizations/SingleValueIcon';
import SingleValueRadial from '@splunk/dashboard-visualizations/SingleValueRadial';
import Image from '@splunk/dashboard-visualizations/Image';
import Markdown from '@splunk/dashboard-visualizations/Markdown';
import Ellipse from '@splunk/dashboard-visualizations/Ellipse';
import Rectangle from '@splunk/dashboard-visualizations/Rectangle';
import ConnectedLine from '@splunk/dashboard-layouts/visualizations/ConnectedLine';
import Text from '@splunk/dashboard-visualizations/Text';
import Choropleth from '@splunk/dashboard-visualizations/Choropleth';
import ChoroplethSvg from '@splunk/dashboard-visualizations/ChoroplethSvg';
// New Visualizations
import PlatformArea from '@splunk/visualizations/Area';
import PlatformBar from '@splunk/visualizations/Bar';
import PlatformBubble from '@splunk/visualizations/Bubble';
import PlatformChoroplethSvg from '@splunk/visualizations/ChoroplethSvg';
import PlatformColumn from '@splunk/visualizations/Column';
import PlatformEllipse from '@splunk/visualizations/Ellipse';
import PlatformImage from '@splunk/visualizations/Image';
import PlatformLine from '@splunk/visualizations/Line';
import PlatformLinkGraph from '@splunk/visualizations/LinkGraph';
import PlatformMarkdown from '@splunk/visualizations/Markdown';
import PlatformParallelCoordinates from '@splunk/visualizations/ParallelCoordinates';
import PlatformPie from '@splunk/visualizations/Pie';
import PlatformRectangle from '@splunk/visualizations/Rectangle';
import PlatformSankey from '@splunk/visualizations/Sankey';
import PlatformScatter from '@splunk/visualizations/Scatter';
import PlatformSingleValue from '@splunk/visualizations/SingleValue';
import PlatformSingleValueIcon from '@splunk/visualizations/SingleValueIcon';
import PlatformTable from '@splunk/visualizations/Table';

// helpers
import mapValues from 'lodash/mapValues';
import compose from 'lodash/flowRight';
import { withVizConfig } from '@splunk/dashboard-ui/utils/presetUtils';

import {
    withTitle,
    withProgressBar,
    withLastUpdated,
    withMeta,
    withVizSwitcher,
    withToolbar,
    withSetTokens,
} from './utils';

const withCommon = compose([withLastUpdated, withProgressBar, withTitle]);

// these wrappers are needed because the with* HOCs mutate the viz properties that may override each other if we use the same component for more than one viz types.
const ChoroplethUs = (props) => React.createElement(Choropleth, props);
const ChoroplethWorld = (props) => React.createElement(Choropleth, props);
Object.keys(Choropleth).forEach((field) => {
    ChoroplethUs[field] = Choropleth[field];
    ChoroplethWorld[field] = Choropleth[field];
});

const visualizations = {
    'abslayout.line': ConnectedLine,
    'viz.area': withCommon(Area),
    'viz.bar': withCommon(Bar),
    'viz.bubble': withCommon(Bubble),
    'viz.choropleth.svg': withCommon(ChoroplethSvg),
    'viz.column': withCommon(Column),
    'viz.ellipse': Ellipse,
    'viz.fillergauge': withCommon(withVizSwitcher(withToolbar(FillerGauge))),
    'viz.geojson.us': withCommon(withVizSwitcher(withToolbar(ChoroplethUs))),
    'viz.geojson.world': withCommon(
        withVizSwitcher(withToolbar(ChoroplethWorld))
    ),
    'viz.img': Image,
    'viz.line': withCommon(Line),
    'viz.markdown': Markdown,
    'viz.markergauge': withCommon(withVizSwitcher(withToolbar(MarkerGauge))),
    'viz.pie': withCommon(Pie),
    'viz.punchcard': withCommon(withVizSwitcher(withToolbar(Punchcard))),
    'viz.radialgauge': withCommon(RadialGauge),
    'viz.rectangle': Rectangle,
    'viz.scatter': withCommon(Scatter),
    'viz.singlevalue': withCommon(SingleValue),
    'viz.singlevalueicon': SingleValueIcon,
    'viz.singlevalueradial': withCommon(
        withVizSwitcher(withToolbar(SingleValueRadial))
    ),
    'viz.table': withCommon(Table),
    'viz.text': Text,
};

const platformVisualizations = {};
// Add shapes and shape-like special platform visualizations (no title/ descriptions, progress bar, last updated, no toolbar or switcher)
[
    PlatformEllipse,
    PlatformImage,
    PlatformMarkdown,
    PlatformRectangle,
    PlatformSingleValueIcon,
].forEach((vizType) => {
    platformVisualizations[vizType.config.key] = vizType;
});

// Add default platform visualizations (defaults in toolbar and switcher)
[
    PlatformArea,
    PlatformBar,
    PlatformBubble,
    PlatformChoroplethSvg,
    PlatformColumn,
    PlatformLine,
    PlatformLinkGraph,
    PlatformParallelCoordinates,
    PlatformPie,
    PlatformSankey,
    PlatformScatter,
    PlatformSingleValue,
    PlatformTable,
].forEach((vizType) => {
    platformVisualizations[vizType.config.key] = withCommon(
        withVizSwitcher(withToolbar(vizType))
    );
});

// Enhance platform visualization to support setting of tokens through event drilldowns
[
    PlatformArea,
    PlatformBar,
    PlatformBubble,
    PlatformChoroplethSvg,
    PlatformColumn,
    PlatformLine,
    PlatformLinkGraph,
    PlatformParallelCoordinates,
    PlatformPie,
    PlatformSankey,
    PlatformScatter,
    PlatformSingleValue,
    PlatformTable,
].forEach((vizType) => {
    const viz = platformVisualizations[vizType.config.key];
    platformVisualizations[vizType.config.key] = withSetTokens(viz);
});

export default {
    visualizations: {
        // wrap all legacy visualizations with a meta property
        ...mapValues(visualizations, compose([withVizConfig, withMeta])),
        // splunk.viz go here
        ...platformVisualizations,
    },
};
